.statistics {
  width: 300px;
  margin: 20px;
  border: 1px solid #757575;
  border-radius: 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.title {
  display: block;
  height: 50px;
  padding: 15px;
  text-transform: uppercase;
  text-align: center;
  color: #757575;
  font-size: 16px;
}

.stat-list {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding: 0;
}

.item {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  text-align: center;
  padding: 15px;
  width: 20%;
}

.label {
  font-size: 12px;
  color: #757575;
  margin-bottom: 5px;
  color: white;
}

.percentage {
  color: white;
}
